import React, { Component } from 'react';

import './ItemAddForm.css';

export default class ItemAddForm extends Component {

  constructor(props){
    super(props);
    this.state = {
      label: '',
      name : '',
      surname : '',
      data: '',
      textarea: '',
    };
  }
  
  onLabelChange = (e) => { this.setState( {label: e.target.value} ) };
  onNameChange = (e) => { this.setState( {name: e.target.value} ) };
  onSurnameChange = (e) => { this.setState( {surname: e.target.value} ) };
  onDataChange = (e) => { this.setState( {data: e.target.value} ) };
  onTextareaChange = (e) => { this.setState( {textarea: e.target.value} ) };

  onSubmit = (e) => {
    e.preventDefault();
    const { label, name, surname, data, textarea, } = this.state;
    this.setState({ label: '',name : '',surname : '',data: '',textarea: '', });
    const cb = this.props.onItemAdded || (() => {});
    cb(label,name, surname, data, textarea);
    this.props.onClickEvent();
  };

  render() {
    return (
      <div >
        <form
          
          onSubmit={this.onSubmit}>

          <div>–î–æ–±–∞–≤–∏—Ç—å –∫–∞—Ä—Ç–∏–Ω–∫—É</div>
          <input type="text"
                  placeholder="–ò–º—è"
                  value={this.state.name}
                  onChange={this.onNameChange}/>

          <input  type="text"
                  placeholder="–§–∞–º–∏–ª–∏—è"
                  value={this.state.surname}
                  onChange={this.onSurnameChange}/>

          <input type="date"
                  value={this.state.data}
                  onChange={this.onDataChange}
                  required/>
          
          <input type="text"
                className="form-control new-todo-label"
                value={this.state.label}
                onChange={this.onLabelChange}
                placeholder="–ö–∞–∫–æ–µ —Å–æ–±—ã—Ç–∏–µ" />
          <div>
              <div>–ó–∞–º–µ—Ç–∫–∞ –¥–ª—è –ø–æ–¥–∞—Ä–∫–∞</div>
              <textarea 
                      value={this.state.textarea}
                      onChange={this.onTextareaChange}/>
          </div>
          

          <button type="submit"
                  className="btn btn-outline-secondary">–î–æ–±–∞–≤–∏—Ç—å</button>
        </form>
      </div>
    );
  }
}
